.meta {
  font-size: calc(10px + 1vmin);
}

.content {
   font-size: calc(12px + 1vmin);
}

.content > img {
  max-width: 100%;
}

.diagram {
  background-color: #f5f5f5;
  border-radius: 4px;
  border: 1px solid #ccc;
  color: #333;
  display: block;
  font-size: 13px;
  line-height: 1.42857143;
  margin: 0 0 10px;
  padding: 9.5px;
  word-break: break-all;
  word-wrap: break-word;
}

.diagram-text {
  font-size: calc(12px + 1vmin);
  font-style: italic;
  font-weight: 300;
  margin-top: 0px;
  margin-bottom: 25px;

  display: flex;
  align-items: center;
  justify-content: center;
}

h1, h2, h3, h4, h5 {
  line-height: 1.15;
  orphans: 2;
  page-break-after: avoid;
  text-align: left;
  widows: 2;
}

h1 {
  color: #fff;
  font-size: calc(20px + 1vmin);
  padding-bottom: 6pt;
  padding-top: 20pt;
}

h2 {
  color: #fff;
  font-size: calc(14px + 1vmin);
  padding-bottom: 6pt;
  padding-top: 18pt;
}

h3 {
  color: #434343;
  font-size: calc(14px + 1vmin);
  padding-bottom: 4pt;
}

h4 {
  color: #666666;
  font-size: 12pt;
  padding-bottom: 4pt;
}

h5 {
  color: #666666;
  font-size: 11pt;
  padding-bottom: 4pt;
}

h6 {
  color: #666666;
  font-size: 11pt;
  font-style: italic;
  padding-bottom: 4pt;
}
